import { graphql } from "gatsby";
import * as React from "react";
import Header from "../components/header"
import Footer from "../components/footer"
import { StructuredText } from "react-datocms";

const CustomizePage = props => {
    return (
        <>
            <Header siteTitle={`KIA MOTORS`} />
            <div className="container-fluid">
                <div className="row text-center bg-dark">
                    <div className="col-lg-4 col-sm-4">
                        <h2 className="h3 text-light">
                            MODELO
                        </h2>
                    </div>
                    <div className="col-lg-4 col-sm-4">
                        <h2 className="h3 text-light">
                            VEHÍCULO
                        </h2>
                    </div>
                    <div className="col-lg-4 col-sm-4">
                        <h2 className="h3 text-light">
                            PVP. SUGERIDO:
                        </h2>
                    </div>
                </div>
                <div className="row text-center bg-light">
                    <div className="col-lg-4 col-sm-4">
                        <h2 className="h3 text-dark">
                            {props.data.datoCmsVehicle.modelo}
                        </h2>
                    </div>
                    <div className="col-lg-4 col-sm-4">
                        <h3 className="h3 text-dark">
                            {props.data.datoCmsVehicle.nombre}
                        </h3>
                    </div>
                    <div className="col-lg-4 col-sm-4">
                        <h3 className="h3 text-dark">
                            ${props.data.datoCmsVehicle.precio}.00
                        </h3>
                    </div>
                </div>
            </div>
            <div className="container-sm my-5">
                <div className="row d-flex justify-content-center align-items-center">
                    <div className="col-lg-6 col-sm-6">
                        <img src={props.data.datoCmsVehicle.imagen.url} className="img-fluid" alt="N/A" />
                    </div>
                    <div className="col-lg-6 col-sm-6">
                        <StructuredText data={props.data.datoCmsVehicle.descripcion.value} />
                    </div>
                </div>
            </div>
            <div className="container-sm my-2">
                <div className="row ">
                    <div className="col-lg-6 col-sm-6">
                        <div className="card">
                            <div className="card-body">
                                <h2 className="card-title text-center">MOTOR: {props.data.datoCmsVehicle.motor.titulo}</h2>
                                <p className="card-text"><StructuredText data={props.data.datoCmsVehicle.motor.descripcion.value} /></p>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-6 col-sm-6">
                        <div className="card">
                            <div className="card-body">
                                <h2 className="card-title text-center">TRANSMISIÓN: {props.data.datoCmsVehicle.transmision.titulo}</h2>
                                <p className="card-text"><StructuredText data={props.data.datoCmsVehicle.transmision.descripcion.value} /></p>
                            </div>
                        </div>
                        <div className="card mt-3">

                            {props.data.datoCmsVehicle.paquetes.map(paquete => (
                                <div className="card-body">
                                    <h2 className="card-title text-center">PAQUETE: {paquete.titulo}</h2>
                                    <p className="card-text"><StructuredText data={paquete.descripcion.value} /></p>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
            <div className="container-sm text-center my-5">
                <a className="btn btn-lg btn-danger" href="/JAMSTACK/catalog">REGRESAR</a>
            </div>
            <Footer />
        </>
    )
}

export const query = graphql`
   query VehicleQuery($vehicleId: String) {
        datoCmsVehicle(originalId: {eq: $vehicleId}) {
            nombre
            precio
            descripcion {
                 value
            }
            modelo
            motor {
                titulo
                descripcion {
                    value
                }
            }
            imagen {
                url
            }
            transmision {
                titulo
                descripcion {
                    value
                }
            }
            paquetes {
                titulo
                descripcion {
                    value
                }
             }
        }
    }
`


export default CustomizePage;